(function() {
  'use strict';

  angular
    .module('app', 
      [ 
        'ui.router',
        'ngMaterial', 
        'toastr'
      ]
    )
})();

(function() {
  'use strict';

  angular
    .module('app')
    .config(routeConfig);

  function routeConfig($stateProvider) {    
    $stateProvider
        .state('home', {
          url: '/home',
          template: '<home></home>'
        });              
  }

})();

(function() {
  'use strict';
  /**
     * @ngdoc component
     * @name app.component:home
     *
     * @description
     * Feature home. Load all necesary data for his childs components
     */
  angular
    .module('app')
    .component('home', {
      templateUrl: 'app/features/home/home.html',
      controller: HomeController,
      controllerAs: 'vm'
    });

    /** @ngInject */
    function HomeController(techs, toastr, creation) {
      var vm = this;
      vm.pageData = {
        section: 'home',
        sectionText: 'Frontend Revolution based on opensource',
        creationDate: creation.getDate(),
        contentType: 'techs',
        contentData: getTechs(),
        logoURL: 'assets/images/angular-basic-git.png'
      };
      
      /**
       * @ngdoc function
       * @name app.component#getTechs
       * @methodOf  app.component:home
       * @requires Techs
       * @description
       * Get all guide items using service techs
       * @returns {object} techs
       */
      function getTechs() {
        var techsItems = techs.getTechs();
        var rankCount = 0;
        
        angular.forEach(techsItems, function(awesomeThing) {
          awesomeThing.rank = rankCount;
          rankCount = rankCount++;
        });
        
        toastr.info('Techs Loaded','Info');
        return techsItems;
      }
    }
  
  
})();

(function() {
  'use strict';

  angular
    .module('app')
    .config(routeConfig);

  function routeConfig($stateProvider) {
    $stateProvider
      .state('about', {
        url: '/about',
        template: '<about></about>'
      });
  }

})();

(function() {
  'use strict';
  /**
     * @ngdoc component
     * @name app.component:about
     *
     * @description
     * Feature about. Load all necesary data for his childs components
     */
  angular
    .module('app')
    .component('about', {
      templateUrl: 'app/features/about/about.html',
      controller: AboutController,
      controllerAs: 'vm'
    });

    /** @ngInject */
    function AboutController(guideItems, toastr, creation) {
      var vm = this;
      vm.pageData = {
        section: 'about',
        sectionText: 'About Section',
        creationDate: creation.getDate(),
        contentType: 'guide-item',
        contentData: getGuides(),
        logoURL: 'assets/images/serenity/serenity.png'
      };
      
      /**
       * @ngdoc function
       * @name app.component#getGuides
       * @methodOf  app.component:about
       * @requires guideItem
       * @description
       * Get guides using service guideItems
       * @returns {object} guideItems
       */
      function getGuides() {
        toastr.info('Guide Items Loaded','Info');
        return guideItems.getItems();
      }
    }
  
})();
